.item {
  border: 1px solid;
  border-color: #717485;
  justify-content: center;
  align-items: center;
  color: #9b9eaf;
}
.nav-button {
  margin-top: 16px;
  margin-left: 10px;
  margin-bottom: 16px;
  border-radius: 10px;
  border: 1px solid;
  font-size: 20px;
}
.item:hover {
  color: #ffffff;
  background-color: #242834;
  transition: background-color 0.1s;
}
.bar-container {
  display: flex;
  justify-content: start;
  gap: 5px;
  z-index: 1;
}
